@import "helpers/stylings/_index";

.wrapper {
  .container {
    background: rgba(255, 255, 255, 0.2);
    padding: 40px 10px;

    @include media-query("pc") {
      padding: 40px 120px;
    }

    @include media-query("tl") {
      padding: 40px 60px;
    }

    .sectionTitle {
      font-size: 50px;
      margin: 0 0 20px;
      text-transform: uppercase;
    }

    .eventListContainer {
      @include align-center;
      flex-wrap: wrap;
      margin-bottom: 20px;
      width: 100%;

      & > div:last-child {
        margin-right: 0;
      }

      .noUpcomingEvents {
        background: rgba(0, 0, 0, 0.41);
        border-radius: 50px;
        color: $white;
        font-size: 20px;
        padding: 10px;
      }
    }

    .viewMore {
      background: rgba(255, 255, 255, 0.49);
      border: 2px solid $white;
      font-weight: bold;

      &:hover {
        background: $black;
        color: $white;
      }
    }
  }
}
