@import "helpers/stylings/_index";

.wrapper {
  border-bottom: 10px solid white;
  width: 100vw;

  .container {
    background: rgba(255, 255, 255, 0.43);
    padding: 40px 10px;

    @include media-query("pc") {
      padding: 40px 120px;
    }

    @include media-query("tl") {
      padding: 40px 60px;
    }

    .sectionTitle {
      font-size: 50px;
      margin: 0;
      padding-bottom: 20px;
      text-transform: uppercase;
    }

    .mapIcon {
      height: 70px;
      margin-bottom: 20px;
      width: 80px;
    }

    .addressContainer {
      background: rgba(0, 0, 0, 0.73);
      border-radius: 10px;
      color: $white;
      font-size: $fontXl;
      margin-bottom: 30px;
      padding: 10px 20px;
      text-transform: uppercase;
    }

    .viewDirection {
      background: rgba(0, 0, 0, 0.1);
      border: 2px solid $white;
      border-radius: 5px;
      color: $black;
      font-weight: bold;
      padding: 10px 15px;
      text-decoration: none;

      &:hover {
        animation: viewDirection 0.2s ease-in forwards;
      }
    }
  }
}

// Overwrite default keyframes
@include media-query(sp) {
  @keyframes slidingUp {
    0% {
      opacity: 0;
      transform: translateY(50px);
    }
    100% {
      opacity: 1;
      transform: translateY(0px);
    }
  }

  @keyframes slidingDown {
    0% {
      opacity: 1;
      transform: translateY(0);
    }
    100% {
      opacity: 0;
      transform: translateY(50px);
    }
  }
}

@keyframes viewDirection {
  0% {
    background: rgba(0, 0, 0, 0.1);
    color: $black;
  }
  100% {
    background: $black;
    color: $white;
  }
}
