@import "helpers/stylings/_index";

.wrapper {
  @include align-center();

  flex-direction: column;
  padding: 20px 0;

  .title {
    font-size: 50px;
    text-transform: uppercase;
  }

  .formWrapper {
    @include align-center;
    align-items: flex-start;
    flex-direction: column;

    .textField {
      background: $white;
      border-radius: 4px;
      margin: 20px 0 10px;
      width: 350px;
    }

    .btnContainer {
      @include align-center;
      width: 350px;

      .btnSend {
        background: $white;
        border-color: rgba(0, 0, 0, 0.24);
        color: rgba(0, 0, 0, 0.56);
        margin-top: 30px;
        width: 150px;

        &:hover {
          background: $black;
          color: $white;
        }
      }
    }

    .errorMessage {
      background: rgba(255, 255, 255, 0.4);
      border-radius: 10px;
      color: red;
      font-size: $fontS;
      padding: 0 8px;
    }
  }
}
