@import "helpers/stylings/_index";

.wrapper {
  padding: 10px 0;

  @include media-query("pc") {
    padding: 20px 120px;
  }

  @include media-query("tl") {
    padding: 20px 60px;
  }

  .sectionTitle {
    @include media-query("sp") {
      font-size: 35px;
    }
    font-size: 50px;
    margin: 0;
    text-transform: uppercase;
  }

  .schedules {
    font-size: $fontXl;
    margin: 20px 0;
    width: 100%;
  }

  .liveStream {
    @include align-center;
    flex-direction: column;

    .liveStreamText {
      font-size: $fontL;
      max-width: 500px;
      margin-bottom: 10px;
    }

    .fbLink {
      @include align-center;

      border: 1px solid;
      border-radius: 5px;
      padding: 3px;
      text-decoration: none;

      &:hover {
        background: $fb;
        color: $white;
      }
    }
  }
}

.serviceTypeWrapper {
  @include align-center();

  flex-direction: column;
  margin-bottom: 20px;
  width: 100%;

  .typeHeader {
    @include align-center();
    margin-bottom: 20px;
  }

  .typeName {
    margin-right: 10px;
  }

  .typeImage {
    @include media-query("sp") {
      height: 200px;
      max-width: 350px;
    }

    background: $bg_default;
    background-size: cover;
    background-position: center;
    border: 15px solid $white;
    border-radius: 5px;
    box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
    height: 350px;
    max-width: 500px;
    position: relative;
    transition: box-shadow 0.5s;
    width: 100%;
    will-change: transform;

    &:hover {
      box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
    }

    .flipImage {
      background-size: cover;
      background-position-x: 50%;
      height: 100%;
      position: absolute;
      width: 100%;
    }
  }
}
