@mixin align-center() {
  align-items: center;
  display: flex;
  justify-content: center;
}

@mixin media-query($mqType) {
  @if $mqType == "sp" {
    @media (max-width: 743px) {
      @content;
    }
  } @else if $mqType == "tl" {
    @media (min-width: 744px) and (max-width: 1023px) {
      @content;
    }
  } @else {
    @media (min-width: 1024px) {
      @content;
    }
  }
}
