@import "helpers/stylings/_index";

.wrapper {
  background: $white;
  border: 1px solid $white;
  border-radius: 10px;
  cursor: pointer;
  margin: 0 10px 30px 0;
  max-width: 235px;
  width: 235px;

  &:hover {
    border: 1px solid rgba(0, 0, 0, 0);
    box-shadow: 0 4px 25px 0 rgba(0, 0, 0, 0.3);
  }

  .eventBanner {
    border-radius: 10px 10px 0 0;
    margin-bottom: -3px;
    width: 100%;
    min-height: 176px;
    max-height: 176px;
  }

  .eventTitleWrapper {
    background-color: $black;
    color: $white;
    max-height: 48px;
    line-height: 24px;
    padding: 10px 15px;
    text-align: start;
    text-transform: uppercase;

    .title {
      font-weight: bold;
    }
  }

  .eventLocation {
    @include align-center;
    color: $bg_default;
    justify-content: flex-start;
    padding: 10px;
  }

  .ellipsis {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
}
