@import "helpers/stylings/_index";

.wrapper {
  .hasBGShadow {
    background: rgba(255, 255, 255, 0.35);
  }

  .container {
    min-height: 100vh;
    min-width: 100vw;
    max-width: 100vw;
    position: relative;
    text-align: center;

    .children {
      padding-bottom: 132px;
    }
  }
}

.langIcon {
  width: 24px;
  margin-right: 10px;
}

.i18n {
  &#{&} {
    background: $white;
    bottom: 10px;
    box-shadow: 0 0 4px 0 rgb(0, 0, 0);
    position: fixed;
    right: 10px;
    z-index: 1100;

    &:hover {
      background-color: $black;
      color: $white;
    }
  }
}
