@import "helpers/stylings/_index";

.topHeader {
  &#{&} {
    background-color: $bg_default;
    box-shadow: none;
    left: 0;
    padding: 10px 0;
    right: unset;
    top: 0;
    width: 100vw;
  }
}

.scrolledHeader {
  &#{&} {
    animation: showing 0.3s ease-out;
    background-color: rgba(255, 255, 255, 0.92);
    color: $black;
  }
}

.fixedStyleHeader {
  &#{&} {
    background-color: rgb(255, 255, 255);
    color: $black;
    position: sticky;
  }
}

@keyframes showing {
  0% {
    opacity: 0.4;
  }
  100% {
    opacity: 1;
  }
}

.wrapper {
  .logoContainer {
    @include align-center();
    cursor: pointer;
    justify-content: space-between;

    .logo {
      height: 35px;
      transition: transform 0.2s;

      &:hover {
        transform: scale(1.1);
      }
    }
  }

  .toolBarContainer {
    @include align-center();
    justify-content: space-between;
  }

  .HeaderLinkContainer {
    @media (max-width: 743px) {
      display: flex;
      flex-direction: row-reverse;
    }
  }
}

.drawerPaper {
  &#{&} {
    background-color: rgba(0, 0, 0, 0.75);
    padding: 10px;
  }
}

// HeaderLinks
.btnFormat {
  &#{&} {
    color: $white;
    font-size: 16px;
    font-weight: bold;

    a {
      all: unset;

      &:visited {
        color: $white;
        text-decoration: none;
      }

      &:hover {
        color: $primary_color;
      }
    }
  }
}

.btnSelected {
  &#{&} {
    a:visited {
      color: $primary_color;
    }
  }
}

.hideBtn {
  &#{&} {
    display: none;
  }
}

.textBlack {
  &#{&} {
    color: $black;
    a {
      all: unset;

      &:visited {
        color: $black;
        text-decoration: none;
      }

      &:hover {
        color: $primary_color;
      }
    }
  }
}
