@import "helpers/stylings/_index";

.fetchingItems {
  @include align-center;

  height: 50vh;
}

.wrapper {
  .backBtn {
    align-items: center;
    color: #308c55;
    cursor: pointer;
    display: flex;
    font-weight: 600;
    margin-bottom: 20px;
    width: fit-content;

    & > span {
      background: #308c55;
      border-radius: 3px;
      height: 3px;
      width: 20px;
    }

    & > :nth-child(1) {
      transform: rotate(25deg) translate(4px, 1px);
    }

    & > :nth-child(2) {
      transform: rotate(-25deg) translate(-13px, -12px);
    }
  }

  .eventTitleWrapper {
    @include media-query(sp) {
      padding: 15px 10px 30px;
      top: auto;
      z-index: 0;
    }

    & > :last-child {
      display: flex;

      @media (max-width: 550px) {
        align-items: center;
        flex-direction: column;

        & > img {
          margin-bottom: 10px;
        }
      }
    }

    background: $white;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
    box-shadow: 0px 10px 9px -8px rgba(0, 0, 0, 0.33);
    display: flex;
    flex-direction: column;
    margin-bottom: 20px;
    padding: 15px 65px 30px;
    position: sticky;
    top: 65px;
    z-index: 1100;

    .eventBanner {
      border-radius: 10px;
      min-height: 176px;
      margin-right: 10px;
      max-height: 176px;
      width: 235px;
    }

    .eventTitle {
      @media (max-width: 550px) {
        align-items: initial;
      }

      align-items: flex-start;
      display: flex;
      flex-direction: column;

      .eventName {
        font-size: 32px;
        margin: 0 0 10px;
      }

      .fbLink {
        @include align-center;

        background: $fb;
        border: 1px solid;
        border-radius: 5px;
        color: $white;
        padding: 3px;
        text-decoration: none;
      }
    }
  }

  .eventDetailsWrapper {
    @include media-query(sp) {
      flex-direction: column;
      margin: 0 10px;
    }

    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    margin: 0 65px;

    .eventTimeLocationWrapper {
      @include media-query(sp) {
        margin-bottom: 20px;
      }
      width: 100%;

      .eventTimeLocation {
        @include media-query(sp) {
          position: unset;
        }

        background: white;
        border-radius: 5px;
        box-shadow: 0 0 4px 0 rgba(46, 62, 72, 0.12), 0 4px 12px 0 rgba(46, 62, 72, 0.12);
        padding: 10px;
        position: sticky;
        top: 249px;
        width: 100%;

        svg {
          color: $bg_default;
        }

        .eventTimeWrapper {
          align-items: center;
          display: flex;
          justify-content: flex-start;
          margin-bottom: 10px;

          .eventTime {
            display: flex;
            flex-direction: column;
            text-align: start;
          }
        }
        .eventLocationWrapper {
          display: flex;
          justify-content: flex-start;

          .eventLocation {
            display: flex;
            flex-direction: column;
            text-align: start;

            .eventAddress {
              color: $bg_default;
              line-height: 15px;
              margin-bottom: 10px;
            }
          }
        }

        .icon {
          margin-right: 20px;
        }
      }
    }

    .eventDescription {
      margin-right: 20px;
    }
  }
}

.marginBtm10 {
  margin-bottom: 10px;
}
