@import "helpers/stylings/_index.scss";

.wrapper {
  border-bottom: 10px solid white;
  border-top: 10px solid white;
  width: 100vw;

  .container {
    @include media-query("pc") {
      padding: 40px 120px;
    }

    @include media-query("tl") {
      padding: 40px 60px;
    }

    background: rgba(255, 255, 255, 0.2);
    padding: 40px 10px;

    .sectionTitle {
      font-size: 50px;
      margin: 0 0 20px;
      text-transform: uppercase;
    }

    .ministryList {
      @include align-center;
      flex-wrap: wrap;

      .ministryItem {
        margin-bottom: 20px;
        max-width: 300px;
        min-width: 300px;

        &:not(:last-child) {
          @media (max-width: 619px) {
            margin-right: 0;
          }

          margin-right: 20px;
        }

        .summaryContainer {
          background: rgba(0, 0, 0, 0.64);
          border-radius: 10px 10px 0 0;
          color: white;
          display: flex;
          flex-direction: column;
          min-height: 250px;
          padding: 15px;

          .ministryName {
            font-size: 24px;
            font-weight: bold;
            padding-bottom: 10px;
          }

          .summary {
            line-height: 20px;
            text-align: justify;
          }
        }

        .banner {
          border-radius: 0 0 10px 10px;
          height: 250px;
          margin-bottom: 20px;
          width: 100%;
        }

        .seeMore {
          background: rgba(255, 255, 255, 0.49);
          border: 2px solid $white;
          font-weight: bold;

          &:hover {
            background: $black;
            color: $white;
          }
        }
      }
    }
  }
}
