@import "helpers/stylings/_index";

.wrapper {
  @include align-center();

  @include media-query("pc") {
    padding: 40px 120px;
  }

  @include media-query("tl") {
    padding: 40px 60px;
  }

  background: $white;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
  flex-direction: column;
  padding: 40px 10px;

  .sectionTitle {
    font-size: 50px;
    margin-bottom: 30px;
    text-transform: uppercase;
  }

  .pastorName {
    font-size: 30px;
    margin-bottom: 20px;
  }

  .detailsContainer {
    @include align-center;
    flex-direction: column;

    .pastorImg {
      border-radius: 50%;
      height: 250px;
      margin-right: 30px;
      min-width: 250px;
      width: 250px;
    }

    .pastorMsg {
      display: flex;
      flex-direction: column;
      line-height: 24px;
      max-width: 550px;
      text-align: justify;

      & > span {
        margin-bottom: 10px;
      }
    }
  }
}
