@import "helpers/stylings/_index";

.wrapper {
  @include align-center();
  background: $bg_default;
  bottom: 0;
  color: $white;
  flex-direction: column;
  height: 132px;
  position: absolute;
  width: 100%;

  .iconContainer {
    @include align-center();
    justify-content: space-between;
    width: 80px;

    .fbIcon {
      background: $fb;
      border-radius: 5px;
      color: $white;
    }

    .cogIcon {
      background-color: $white;
      border-radius: 6px;
      height: 27px;
      padding: 3px;
      width: 27px;
    }
  }
}
