@import "helpers/stylings/_index";

.wrapper {
  @include media-query(sp) {
    flex-direction: column;
    padding: 0px 10px;
  }

  @include media-query(tl) {
    padding: 20px;
  }

  display: flex;
  justify-content: space-between;
  padding: 20px 60px 0;

  .eventTypesContainer {
    @include media-query(sp) {
      border-radius: 0px;
      display: flex;
      height: 100%;
      margin: 0 -10px 30px;
      top: 65px;
      z-index: 1100;
    }

    @include media-query(tl) {
      margin-right: 20px;
    }

    background: $white;
    box-shadow: 0 0 4px 0 rgba(46, 62, 72, 0.12), 0 4px 12px 0 rgba(46, 62, 72, 0.12);
    border-radius: 5px;
    height: 60px;
    margin-right: 50px;
    padding: 20px;
    padding-right: 0;
    position: sticky;
    top: 70px;
    min-width: 220px;

    .eventType {
      @include media-query(sp) {
        @include align-center;
        margin: 0;
      }

      cursor: pointer;
      display: flex;
      font-weight: 600;
      margin-bottom: 20px;
      text-transform: uppercase;
      width: 98%;
    }

    .selected {
      @include media-query(sp) {
        border: none;
      }

      border-right: 5px solid $primary_color;
      color: $primary_color;
    }
  }

  .noEventContainer {
    flex-direction: row;
  }

  .hasEventContainer {
    flex-direction: column;
  }

  .eventSummaryContainer {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: 100%;

    .yearTitle {
      background: #00000080;
      border-radius: 10px;
      box-shadow: 0 0 4px 0 rgba(46, 62, 72, 0.12), 0 4px 12px 0 rgba(46, 62, 72, 0.12);
      color: white;
      font-size: 18px;
      font-weight: bold;
      margin-bottom: 20px;
    }

    .eventPerYear {
      @include align-center;
      flex-wrap: wrap;
    }

    .noUpcomingEvents {
      @include align-center;
      @include media-query(sp) {
        height: 58px;
      }

      background: $white;
      border: 1px solid rgba(0, 0, 0, 0.31);
      border-radius: 10px;
      height: 100%;
      padding: 0 20px;

      &:hover {
        border: 1px solid rgba(0, 0, 0, 0);
        box-shadow: 0 0 4px 0 rgba(46, 62, 72, 0.12), 0 4px 12px 0 rgba(46, 62, 72, 0.12);
      }
    }
  }
}
