.scrolledLeft {
  animation: slidingLeft 1s ease-in;
  will-change: opacity;
}

.scrolledLeftOut {
  animation: slidingLeftOut 1s ease-out;
  opacity: 0;
  will-change: opacity;
}

.scrolledRight {
  animation: slidingRight 1s ease-in;
  will-change: opacity;
}

.scrolledRightOut {
  animation: slidingRightOut 1s ease-out;
  opacity: 0;
  will-change: opacity;
}

.scrolledUp {
  animation: slidingUp 1s ease-in;
  will-change: opacity;
}

.scrolledDown {
  animation: slidingDown 1s ease-out;
  opacity: 0;
  will-change: opacity;
}

@keyframes slidingLeft {
  0% {
    opacity: 0;
    transform: translateX(-200px);
  }
  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes slidingLeftOut {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(-200px);
  }
}

@keyframes slidingRight {
  0% {
    opacity: 0;
    transform: translateX(200px);
  }
  100% {
    opacity: 1;
    transform: translateX(0px);
  }
}

@keyframes slidingRightOut {
  0% {
    opacity: 1;
    transform: translateX(0);
  }
  100% {
    opacity: 0;
    transform: translateX(200px);
  }
}

@keyframes slidingUp {
  0% {
    opacity: 0;
    transform: translateY(100px);
  }
  100% {
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes slidingDown {
  0% {
    opacity: 1;
    transform: translateY(0);
  }
  100% {
    opacity: 0;
    transform: translateY(100px);
  }
}
