@import "helpers/stylings/_index.scss";

.wrapper {
  width: 100vw;

  .container {
    @include media-query("pc") {
      padding: 40px 120px;
    }

    @include media-query("tl") {
      padding: 40px 60px;
    }

    background: rgba(255, 255, 255, 0.2);
    padding: 40px 10px;

    .sectionTitle {
      font-size: 50px;
      margin: 0 0 20px;
      text-transform: uppercase;
    }

    .description {
      background: rgba(0, 0, 0, 0.41);
      border-radius: 50px;
      color: $white;
      font-size: 25px;
      padding: 20px;
    }

    .joinLifeGroup {
      background: rgba(255, 255, 255, 0.49);
      border: 2px solid $white;
      font-weight: bold;

      &:hover {
        background: $black;
        color: $white;
      }
    }
  }
}
