@import "helpers/stylings/_index.scss";

.wrapper {
  @include media-query(sp) {
    flex-direction: column;
    width: 300px;
  }

  background: $black;
  border-radius: 10px;
  color: $white;
  display: flex;
  flex-direction: row;
  margin-bottom: 20px;

  .lgBanner {
    @include media-query(sp) {
      border-radius: 10px 10px 0 0;
      width: 100%;
    }

    border-radius: 10px 0 0 10px;
    height: 240px;
    width: 250px;
  }

  .lgDetails {
    @include align-center;
    @include media-query(sp) {
      min-width: 260px;
      max-width: 260px;
    }

    align-items: flex-start;
    flex-direction: column;
    min-width: 300px;
    max-width: 300px;
    padding: 20px;
    text-align: start;

    .title {
      font-size: 20px;
      font-weight: bold;
      margin-bottom: 10px;
    }

    .direction {
      border: 1px solid $white;
      border-radius: 5px;
      color: $white;
      font-weight: bold;
      margin: 10px 0;
      padding: 5px 10px;
      text-decoration: none;

      &:hover {
        background: $white;
        color: $black;
      }
    }
  }
}
