@import "helpers/stylings/_index";

.wrapper {
  .bannerWrapper {
    @include align-center;
    height: 100vh;
    position: relative;
    width: 100vw;
  }

  .bannerImage {
    height: 100%;
    position: absolute;
    width: 100%;
    will-change: opacity;
  }

  .bannerText {
    @include align-center();
    @include media-query("sp") {
      height: 100vh;
      padding: 0;
    }

    background-color: rgba(0, 0, 0, 0.54);
    color: $white;
    flex-direction: column;
    padding: 30px 0;
    position: absolute;
    width: 100vw;

    .welcome {
      @include media-query(sp) {
        font-size: $fontL;
        font-weight: bold;
      }
      font-size: $fontSubTitle;
      margin-bottom: 10px;
    }

    .churchName {
      display: flex;
      flex-direction: column;
      margin-bottom: 20px;

      .tlc {
        @include media-query(sp) {
          font-size: 40px;
        }
        font-size: $fontTitle;
      }
    }

    .joinus {
      @include media-query(sp) {
        font-size: $fontL;
      }

      display: flex;
      flex-direction: column;
      font-family: none;
      font-size: $fontSubTitle;
      font-weight: bold;
      max-width: 650px;
      padding: 0 20px;
    }
  }
}
